.brand {
  font-family: 'Bebas Neue';
  /* color: white; */
  color: black;
  font-size: 1.5rem;
  margin: 0.5rem;
  padding-left: 1rem;
}


.menu {
  display: grid;
  grid-template-columns: repeat(4, 7.2rem);
  /* grid-template-columns: auto; */
  /* grid-gap: 0%; */
  list-style: none;
  align-items: center;
  text-align: center;
  justify-content: end;
  /* padding-right: 2rem; */
}

.nav-links {
  text-decoration: none;
  /* color: white; */
  color: rgb(119, 119, 119);
  font-size: 1 rem;
  /* margin-right: 1.5rem; */
  padding: 0rem 1rem;
  white-space: nowrap;
  font-family:Sans-Serif;
  
}

.nav-links:hover {
  color: black;
  /* background-color: white; */
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
}

.NavItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* font-size: 1.2rem; */
  padding: 0rem 1rem;
  /* background-color: rgba(0, 0, 0, 0) ; */
  /* background-color: black ; */
  background-color: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  height: auto;
  border-radius: 10px;
  position: fixed;
  z-index: 2;
  /* position: absolute; */
  /* top: 25px; */
  /* bottom: 25px; */
  left: 50%;
  /* right: 50%; */
  transform: translate(-50%);
  /* margin-left: 1.5rem; */
  /* margin-right: 15px; */
  
}


.NavItem .fa-bars, 
.NavItem .fa-times {
  color: black;
}

.icon-menu {
  display: none;
}

.isCurrent {
  color: black;
  font-weight: bold;
}


@media screen and (max-width: 900px) {
  .NavItem{
    z-index: 99;
  }
  
  .menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(100, 100, 100, 0.701);
    backdrop-filter: blur(5px);
    /* border-radius: 20px; */
    width: 100%;
    height: auto;
    position: absolute;
    top: auto;
    left: 0%;
    top: -1000% ;
    align-items: stretch;
    padding: 40px 0px 20px 0px ;
    z-index: -1;
    transition: all 0.2s ease-in-out;
  }

  .menu.active {
    left: 0%;
    top: -40%;
    right: -110%;
  }

  .nav-links {
    display: block;
    width: 100%;
    padding: 1.5rem 0rem;
    color: white;
    font-size: 1rem;
  }

  .nav-links:hover {
    background: rgba(0, 0, 0, 1);
    color: white;
  }

  .icon-menu {
    display: block;
    margin-right: 1rem;
  }

}


