.contact {
    background-color: #0b0909;
    border-radius: 10px;
    margin-bottom: 2rem;
    margin-top: 2rem;
    width: 97%;
    padding-top: 1rem;
}

.contact h3 {
    font-family: inherit;
    font-size: 30px;
    margin-bottom: 3rem;
}

.isikontak {
    line-height: 0.7;
}

.kota {
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
}

.email {
    font-family: 'Poppins', sans-serif;
    font-size: 25px;
}

.hp {
    font-family: 'Open Sans', sans-serif;
    letter-spacing: 0.2rem;
    font-size: 16px
}

.contact .iconmedsos {
    margin-top: 3rem;
}

@media screen and (max-width: 900px) {
    .contact {
        /* text-align: center; */
        height: auto;
        border-radius: 10px;
        width: 92%;
        padding: auto;
        margin-left: 0%;
        margin-right: 0%;
        margin-top: 2%;
    }
    
    .contact h3 {
        font-family: inherit;
        font-size: x-large;
        margin-bottom: auto;
    }
    
    .contact .isikontak {
        line-height: 0.7;
    }
    
    .contact .kota {
        font-family: 'Open Sans', sans-serif;
        font-size: medium;
    }
    
    .contact .email {
        font-family: 'Poppins', sans-serif;
        font-size: large;
    }
    
    .contact .hp {
        font-family: 'Open Sans', sans-serif;
        font-size: small;
    }
    
    .contact .iconmedsos {
        margin: 0%;
    }
}