.portofolio {
    background-color: #44444c;
    padding: 1rem;
    border-radius: 10px;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.web {
    width: 25rem;
    border-radius: 10px;
    margin: 1rem 2rem 4rem 2rem;
}

.content {
    width: 25rem;
    border-radius: 10px;
    margin: 1rem 2rem 2rem 2rem;
}

@media screen and (max-width: 900px) {
    .portofolio {
        text-align: center;
        height: auto;
        width: 85%;
        /* padding: 2%; */
        border-radius: 10px;
        margin-top: 6%;
        margin-left: auto;
        margin-right: auto;
    }

    .portofolio h2 {
        font-size: x-large;
        margin: 0%;
        margin-bottom: 3%;
    }
    
    .web {
        width: 80%;
        border-radius: 10px;
        margin: 2%;
    }
    
    .content {
        width: 80%;
        border-radius: 10px;
        margin: 0%;
    }
}