.foto {
    width: 25rem;
    margin-top: 4.5rem;
    margin-right: -5%;
    border-radius: 1rem;
    /* margin-left: -20rem; */
    /* float: right; */
}

.Content {
    /* background-color: black; */
    background-color: #0b0909;
    backdrop-filter: blur(100px);
    text-align: left;
    width: 47rem;
    /* width: auto; */
    height: 31.3rem;
    margin-top: 4.5rem;
    margin-left: -2rem;
    border-radius: 1rem;
    float: left;
    padding: 0rem 2rem 0 2rem;
}


.detail {
    font-family: 'Open Sans', sans-serif;
    margin-top: -0.5rem;
}

.work {
    margin-top: 1.3rem;
}

h2 {
    margin-top: 2.3rem;
}

button {
  margin-bottom: 0.1rem;
  font-size: 18px;
  color: #e1e1e1;
  font-family: inherit;
  font-weight: 800;
  cursor: pointer;
  position: relative;
  border: none;
  background: none;
  text-transform: uppercase;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: color;
}

button:focus,
button:hover {
  color: #fff;
}

button:focus:after,
button:hover:after {
  width: 100%;
  left: 0%;
}

button:after {
  content: "";
  pointer-events: none;
  bottom: -2px;
  left: 50%;
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: #fff;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: width, left;
}

.isikontak {
    margin-top: 0.5rem;
}

.kontakkiri {
    float: left;
    margin-right: 2rem;
    /* background-color: aquamarine; */
}

