.kontak {
    margin-left: 0%;
    padding: 10px 15px;
    border: unset;
    border-radius: 15px;
    color: black;
    z-index: 1;
    /* background: rgb(255, 98, 0); */
    background-color: white;
    position: relative;
    font-weight: 1000;
    font-size: 15px;
    /* -webkit-box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27); */
    /* box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27); */
    transition: all 250ms;
    overflow: hidden;
}
   
.kontak::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    border-radius: 15px;
    /* background-color: white; */
    background: rgb(255, 98, 0);
    z-index: -1;
    /* -webkit-box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27); */
    /* box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27); */
    transition: all 250ms
}

.kontak::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    border-radius: 15px;
    /* background-color: white; */
    background: rgb(255, 98, 0);
    z-index: -1;
    /* -webkit-box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27); */
    /* box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27); */
    transition: all 250ms
}
   
.kontak:hover {
    color: white;
}
   
.kontak:hover::before {
    width: 100%;
}

