.videografi {
    /* background-color: #3e3e42; */
    background-color: #0b0909;
    margin: 0 2rem 2rem 0 ;
    border-radius: 10px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-right: -3rem;
    padding: 2rem;
    height: 15.5rem;
    /* float: right; */
}
.videografi h3 {
    margin: 0rem 0rem 2rem 0rem;
    font-family: inherit;
    font-weight: 800;
}

a {
  text-decoration: none;
  color: white;
}

.videografi .isi {
    float: right;
    text-align: right;
    margin-right: 2rem;
    margin-left: 1rem;
    font-family: 'Open Sans', sans-serif;
}

.videografi .isi p {
  margin-top: -1rem;
}

.videografi .skill {
    float: left;
    margin-top: 4rem;
    margin-right: -0.7rem;
}

.videografi .skill a {
    /* background-color: #212121; */
    background-color: #44444c;
    border-radius: 1rem;
    padding: 0.5rem;
    /* align-items: center; */
    font-family: inherit;
    margin: 1rem;
}


.videografi .fa {
  margin-right: 0px;
}

.videografi .button {
  display: inline-block;
  padding: 12px 24px;
  border: 1px solid white;
  border-radius: 4px;
  transition: all 0.2s ease-in;
  position: relative;
  overflow: hidden;
  font-size: 19px;
  color: white;
  z-index: 1;
  margin-right: 0%;
 }
 
 .button:before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%) scaleY(1) scaleX(1.25);
  top: 100%;
  width: 140%;
  height: 180%;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
 }
 
 .button:after {
  content: "";
  position: absolute;
  left: 55%;
  transform: translateX(-50%) scaleY(1) scaleX(1.45);
  top: 180%;
  width: 160%;
  height: 190%;
  background-color: #39bda7;
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
 }
 
 .button:hover {
  color: #ffffff;
  border: 1px solid #39bda7;
 }
 
 .button:hover:before {
  top: -35%;
  background-color: #39bda7;
  transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
 }
 
 .button:hover:after {
  top: -45%;
  background-color: #39bda7;
  transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
 }

 @media screen and (max-width: 900px) {
  .videografi {
    height: auto;
    margin:auto;
    width:auto;
    border-radius: 10px;
    /* margin-top: 0%; */
    /* margin-bottom: 10%; */
    margin: 5%;
    /* margin-left: 1rem; */
    padding: auto
}
.videografi h3 {
  font-family: inherit;
  text-align: center;
  font-weight: 800;
  margin: 0%;
  margin-bottom: 10%;
}

a {
  text-decoration: none;
  color: white;
}

.videografi .isi {
  margin-left: 0%;
  margin-right: 0%;
  text-align: center;
  float: none;
  padding: 0%;
}

.videografi .isi p {
  margin-top: -1rem;
  font-size: 1rem;
  margin-left: auto;
  padding: auto;
}

.videografi .skill {
  float: none;
    margin-top: 5%;
    margin-right: 0%;
    font-size: 1rem;
    margin-bottom: auto;
}

.videografi .skill a {
  font-size: small;
  border-radius: 10px;
    padding: 1%;
    /* align-items: center; */
    font-family: inherit;
    margin: 1%;
}


.videografi .fa {
  margin-left: 0%;
}
}